import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import textField from 'muse-ui/textField';

import Utils from '~/utils';
import { ToastMsg } from '~/entity/toast';
import { IVerificationConfig, CACHE_NAME } from '~/cache';
import { Subject } from 'rxjs/Subject';

import Api from '~/api';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class Comp extends Vue {

    @Action('showToast')
    showToast;

    @Action('getMyInfo')
    getMyInfo;

    edit = false;

    buttonTxt = '';

    BUTTON_TEXT = {
        CHANGE: '更换手机号',
        BIND: '绑定',
        SUCCESS: '绑定成功'
    };

    // @State(state => state.user.user.tel)
    phone: string = null;

    get buttonEnable() {
        if (this.buttonTxt === this.BUTTON_TEXT.CHANGE) {
            return true;
        } else if (this.buttonTxt === this.BUTTON_TEXT.BIND) {
            if (this.phone.length > 0) {
                return true;
            } else {
                return false;
            }
        }
        return false;
    }

    activated() {
        this.edit = false;
        this.buttonTxt = this.BUTTON_TEXT.CHANGE;
        this.phone = this['$store'].state.user.user.tel;
    }
    
    // deactivated() {
        
    // }

    clickButton() {
        if (this.buttonTxt === this.BUTTON_TEXT.CHANGE) {
            
            this.edit = true;
            this.buttonTxt = this.BUTTON_TEXT.BIND;

        } else if (this.buttonTxt === this.BUTTON_TEXT.BIND) {

            const phone = this.phone;

            if (!Utils.checkPhoneNumber(phone)) {
                this.showToast(new ToastMsg('请输入正确的手机号'));
                return;
            }

            const subject = new Subject<string>();
            const subscription = subject.subscribe(async (code) => {
                // 提交验证码
                try {
                    const res = await Api.changeBindPhone(phone, code);
                    
                    // 绑定成功 重新初始化个人资料
                    await this.getMyInfo();
                    this.showToast(new ToastMsg('更换绑定手机成功'));

                    // 页面回退
                    this['$router'].go(-1);
                } catch (e) {
                    this.showToast(new ToastMsg(e.message));
                }  
            });

            const config: IVerificationConfig = {
                phone: this['$store'].state.user.user.tel,
                type: 'bindTelephone',
                subject,
                subscription
            };

            Utils.saveItem(CACHE_NAME.verificationConfig, config);
            // 进入验证码界面
            this['$router'].push({
                name: 'verificationCode'
            });
        }
    }

    editHint() {
        if (!this.edit) {
            this.showToast(new ToastMsg('请先点击下方更换手机号按钮再进行编辑'));
        }
    }
}



